<script>
   /**************************************************************/
   /* AcceptancePlot/ImportancePlot for training app             */
   /* --------------------                                       */
   /* shows plot with score and orthogonal distances             */
   /* and acceptance areas  (both for objects and variables)     */
   /**************************************************************/

   import { Axes, Rectangles, XAxis, YAxis, Box } from 'svelte-plots-basic/2d';
   import { PNG_WIDTH, PNG_HEIGHT, PNG_RES, MARGINS } from '../../../shared/plots/Defaults.js';
   import DatasetsPlotSeries from './DatasetsPlotSeries.svelte';
   import OutcomesPlotSeries from './OutcomesPlotSeries.svelte';

   export let params;                     // JSON with main plot parameters
   export let varselparams;
   export let datasets;                   // JSON with datasets
   export let outcomes;                   // JSON with LOVE outcomes
   export let statsGlobal;
   export let statsBest;
   export let selected = undefined;       // selected variable
   export let intExcluded = [];
   export let seriesColors;

   export let fileName = 'ddsimca-love-outcomes';
   export let pngWidth = PNG_WIDTH;
   export let pngHeight = PNG_HEIGHT;
   export let pngRes = PNG_RES;


   /*****************************************/
   /* Component code                        */
   /*****************************************/

   const fomNames = {
      'sens': 'sensitivity',
      'spec': 'specificity',
      'eff': 'efficiency',
      'sel': 'selectivity'
   };

   // local copies of user defined plot parameters
   let ncomp = 1;
   let fom = 'sens'
   let fomView = 'diff';

   // input dependent but permanent values
   let xLabel = datasets.cal.varAxisTitle;

   /**
    * Function to handle selection of a point or a bar
    * @param e - event object
    */
   function select(e) {
      if (selected === undefined || !outcomes || outcomes.status !== 'done') return; // do not select points if this option is not activated in parent app
      const id = parseInt(e.detail.elementID)
      const title = e.detail.seriesTitle
      if (id >= 0 && title === 'intervals') {
         selected = id;
      }
   }

   /**
    * Function to reset selection
    * @param e - event object
    */
   function resetSelection(e) {
      selected = -1;
   }


   function getLimX(datasets) {
      if (!datasets) return [0, 1];
      const mn = datasets.cal.varvalues[0];
      const mx = datasets.cal.varvalues[datasets.cal.varvalues.length - 1];
      const dx = mx - mn;
      return [mn - dx * 0.10, mx + dx * 0.025];
   }


   // reactive expressions to monitor changes in plot parameters
   $: if (params && params.ncomp) ncomp = params.ncomp;
   $: if (params && params.fom) fom = params.fom;
   $: if (params && params.fomView) fomView = params.fomView;


   // reactive expressions for title, y-axis label and limits
   let title, yLabel, limY;
   $: if (outcomes) {
      if (outcomes.status === 'done') {
         title = 'Variable selection outcomes (A = ' + ncomp + ')';
         yLabel = (fomView === 'diff' ? 'Difference in ' : '') + fomNames[fom];
         limY = fomView === 'diff' ? [-0.5, 0.5] : [0, 1.1];
      } else {
         title = 'Selection in progress...';
         yLabel = ' ';
         limY = [0, 1.1];
      }
   } else {
      title = 'Data and intervals';
      yLabel = ' ';
      limY = [0, 1.1];
   }

   // reactive expression for computing arrays with height and top coordinates
   // of interval rectangles
   let rectTop, rectHeight, nInt = 0;;
   $: if (varselparams && nInt !== varselparams.nInt) {
      const nInt = varselparams.nInt;
      rectTop = Array(nInt).fill(10);
      rectHeight = Array(nInt).fill(22);
   } else {
      nInt = 0;
   }

   // reactive expression to compute coordinates of intervals' rectangles
   let si;
   $: si = varselparams ? {
      left: varselparams.intLeftValues,
      width: varselparams.intWidth,
      top: rectTop,
      height: rectHeight
   } : null;


   // reactive expression to set coordinates of excluded intervals' rectangles
   let si_excl;
   $: if (si && intExcluded.length > 0) {
      const n = intExcluded.length;
      const left = Array(n);
      const width = Array(n);
      const top = Array(n);
      const height = Array(n);
      for (let i = 0; i < n; i++) {
         left[i] = si.left[intExcluded[i]];
         width[i] = si.width[intExcluded[i]];
         top[i] = si.top[intExcluded[i]];
         height[i] = si.height[intExcluded[i]];
      }

      si_excl = {left, width, top, height};
   } else {
      si_excl = null;
   }


   // x-axis limits
   $: limX = getLimX(datasets);
</script>


{#if params && limX && limY}
<Axes
   on:barclick={select}
   on:axesclick={resetSelection}
   margins={MARGINS}
   downloadLinks="hover"
   {fileName}
   {xLabel}
   {yLabel}
   {limX}
   {limY}
   {title}
   {pngWidth}
   {pngHeight}
   {pngRes}
>

   {#if datasets}
   <DatasetsPlotSeries {datasets} {limY}/>
   {/if}


   {#if si_excl }
   <Rectangles
      className="series-bar" title="intervals"
      borderColor="transparent"
      faceColor="#00000040"
      {...si_excl}
   />
   {/if}

   {#if outcomes}
   <OutcomesPlotSeries {selected} {limX} {statsGlobal} {statsBest} {outcomes} {varselparams} {ncomp} {fom} {fomView} {seriesColors} />
   {/if}

   {#if si }
   <Rectangles
      className="series-bar" title="intervals"
      borderColor="#00000010"
      faceColor="#00000005"
      {...si}
   />
   {/if}


   {#if si && selected >= 0}
   <Rectangles
      className="series-bar" title="intevals"
      left={[si.left[selected]]}
      width={[si.width[selected]]}
      top={[si.top[selected]]}
      height={[si.height[selected]]}
      borderColor="#00000010"
      faceColor="#ff000010"
   />
   {/if}


   <Box slot="box" />
   <XAxis slot="xaxis" />
   <YAxis slot="yaxis" showGrid={true} />
</Axes>
{/if}