import { Vector, vector } from 'mdatools/arrays';
import { mean, variance } from 'mdatools/stat';
import { pnorm, qchisq } from 'mdatools/distributions';

import { pcares_get, pcamodel_get } from '../../shared/wasm/dist/pca.js';


export function getSelectivity(f, fCrit, k) {

   let I = f.length;

   const fv = vector(f);
   let fp = fv.sort();
   let ind = fv.sortind();

   // Step 2. If Disc <0
   let Disc = -1;
   let n = 0;
   let m = 0;
   let d = 0;
   let M1 = 0;

   while (Disc < 0) {
      if (n > 0) {
         I = I - 1;
         ind = ind.slice(1, I);
         fp = fp.slice(1, I);
      }
      m = mean(fp);
      d = variance(fp, true);
      M1 = d / (m * m);
      Disc = 4 - 2 * k * M1;
      n = n + 1;
   }

   // Step 3. Calculate  x  by Eq. (18)
   const x = (2 + Math.sqrt(Disc)) / M1;

   // 	Calculate  s and  f'0 by Eq. (19)
   const s = (x - k);
   const f0 = m / x;

   // 	Calculate: z, h, r, p, Mz, Sz by Eq. (20)
   const z = fCrit / f0;
   const h = 1 - 2 * (k + s) * (k + 3 * s) / (3 * (k + 2 * s)**2);
   const r = (h - 1) * (1 - 3 * h);
   const p = (k + 2 * s) / (k + s)**2;
   const Mz = 1 + h * p * (h - 1 - 0.5 * (2 - h) * r * p);
   const Sz = h * Math.sqrt(2 * p) * (1 + 0.5 * r * p);

   // Step 3.	If  is given then calculate  by Eq. (21)
   const beta = pnorm((Math.pow(z / (k + s), h) - Mz) / Sz);

   return 1 - beta;
}


export function getStat(m, r_target, r_alt, global_stats) {
   const alpha = 0.95;

   let sens_global, spec_global, eff_global, sel_global;
   if (!global_stats) {
      sens_global = new Float64Array(m.ncomp);
      spec_global = new Float64Array(m.ncomp);
      eff_global = new Float64Array(m.ncomp);
      sel_global = new Float64Array(m.ncomp);
   } else {
      sens_global = global_stats.sens;
      spec_global = global_stats.spec;
      eff_global = global_stats.eff;
      sel_global = global_stats.sel;
   }

   const Nh = pcamodel_get(m, 'cNh');
   const h0 = pcamodel_get(m, 'ch0');
   const Nq = pcamodel_get(m, 'cNq');
   const q0 = pcamodel_get(m, 'cq0');

   const sens = new Float64Array(m.ncomp);
   const spec = new Float64Array(m.ncomp);
   const sel = new Float64Array(m.ncomp);
   const eff = new Float64Array(m.ncomp);

   const sens_diff = new Float64Array(m.ncomp);
   const spec_diff = new Float64Array(m.ncomp);
   const sel_diff = new Float64Array(m.ncomp);
   const eff_diff = new Float64Array(m.ncomp);

   for (let a = 0; a < m.ncomp; a++) {
      const Nha = Nh[a]
      const h0a = h0[a];
      const Nqa = Nq[a]
      const q0a = q0[a];
      const Nfa = Nha + Nqa;
      const eCrit = qchisq(1 - alpha / 100, Nfa);

      let q, h;

      // compute sensitivity
      q = pcares_get(r_target, 'qdist', a + 1);
      h = pcares_get(r_target, 'hdist', a + 1);
      let sens_v = 0;
      for (let i = 0; i < r_target.nrows; i++) {
         const f = h[i] / h0a * Nha + q[i] / q0a * Nqa;
         sens_v += (f < eCrit);
      }
      sens_v = sens_v / r_target.nrows
      sens[a] = sens_v;
      sens_diff[a] = sens[a] - sens_global[a];

      // compute specificity
      q = pcares_get(r_alt, 'qdist', a + 1);
      h = pcares_get(r_alt, 'hdist', a + 1);
      const f_vec = Array(r_alt.nrows);
      let spec_v = 0;
      for (let i = 0; i < r_alt.nrows; i++) {
         const f = h[i] / h0a * Nha + q[i] / q0a * Nqa;
         spec_v += (f >= eCrit);
         f_vec[i] = f;
      }
      spec_v = spec_v / r_alt.nrows
      spec[a] = spec_v;
      sel[a] = getSelectivity(f_vec, eCrit, Nfa);

      spec_diff[a] = spec[a] - spec_global[a];
      sel_diff[a] = sel[a] - sel_global[a];

      // compute efficiency
      eff[a] = Math.sqrt(sens_v * spec_v);
      eff_diff[a] = eff[a] - eff_global[a];
   }

   return {
      ncomp: m.ncomp,
      sens: {diff: sens_diff, val: sens},
      spec: {diff: spec_diff, val: spec},
      sel: {diff: sel_diff, val: sel},
      eff: {diff: eff_diff, val: eff}
   };
}


