<script>
   /**************************************************************/
   /* LOVE                                                       */
   /* -------------------------------------                      */
   /* shows panel with SIMCA model settings.                     */
   /**************************************************************/

	import { createEventDispatcher } from 'svelte';

   import { pca_fit, pca_predict } from '../../../shared/wasm/dist/pca';
   import { Matrix } from 'mdatools/arrays';
   import { seq } from '../../../shared/utils.js';
   import { getStat } from '../utils';

   import Select from '../../../shared/controls/Select.svelte';
   import Container from '../../../shared/controls/Container.svelte';
   import Button from '../../../shared/controls/Button.svelte';
   import ButtonCancel from '../../../shared/controls/ButtonCancel.svelte';

   // unput
   export let WASM;
   export let model;
   export let datasets;
   export let varselparams;
   export let statsGlobal;
   export let statsBest;
   export let intExcluded;
   export let varsExcluded;
   export let alpha = 0.05;

   // outputs
   export let outcomes;


   /*****************************************/
   /* Component code                        */
   /*****************************************/

   // event dispatcher
   const dispatch = createEventDispatcher();

   let nVars = datasets.cal.values.ncols;
   let intSize = nVars < 30 ? 1 : nVars < 100 ? 5 : 10;
   let intSizeMax = Math.round(nVars / 4);
   let varValues = datasets.cal.varvalues;
   let intSizeOptions = [1, 2, 4, 5, 10, 20, 50].filter(v => v <= intSizeMax);

   // if interval is excluded reset outcomes
   let oldIntExcluded = intExcluded;
   $: if (oldIntExcluded !== intExcluded) {
      oldIntExcluded = intExcluded;
      outcomes = null;
   }

   // reactive expression to compute interval parameters
   $: if (intSize > 0) {
      outcomes = null;

      const nInt = Math.ceil(nVars / intSize);
      const intLeftIndices = Array(nInt);
      const intRightIndices = Array(nInt);
      const xValues = Array(nInt);
      const intLeftValues = Array(nInt);  // shifted values of light boundary for bar plot
      const intWidth = Array(nInt);       // width of the shifted intervals

      let leftInd, leftIndNext, leftNextValNext, leftVal, rightVal, leftNextVal, leftValNext, rightInd, leftMargin, i;
      let limX = [0, 1];
      let limMargin;

      // first (nInt - 1) intervals
      for (i = 0; i < nInt - 1; i++) {
         leftInd = i * intSize;                                      // index of left boundary of this interval
         rightInd = leftInd + intSize - 1;                           // index of right boundary of this interval
         leftIndNext = rightInd + 1;                                 // index of left boundary of next interval

         rightVal = varValues[rightInd]
         leftVal = varValues[leftInd];                               // value for left boundary of this interval
         leftNextVal = varValues[leftInd  + 1];                      // next to previous value
         leftMargin = Math.abs(leftNextVal - leftVal) / 2;           // half distance of the two values (left margin)

         leftValNext = varValues[leftIndNext];                       // value for left boundary of next interval
         leftNextValNext = varValues[leftIndNext  + 1];              // next to previous value

         intLeftIndices[i] = leftInd
         intRightIndices[i] = rightInd;
         intLeftValues[i] = leftVal - leftMargin;
         intWidth[i] = Math.abs(leftValNext - leftVal);
         xValues[i] = (leftVal + rightVal) / 2;

         if (i == 0) {
            limMargin = leftMargin;
            limX[0] = leftVal - 2 * leftMargin;
         }
      }

      // last interval
      i = nInt - 1;
      leftInd = i * intSize;
      rightInd = nVars - 1;
      leftVal = varValues[leftInd];
      rightVal = varValues[rightInd];

      intLeftIndices[i] = leftInd
      intRightIndices[i] = rightInd;
      intLeftValues[i] = leftVal - leftMargin;
      intWidth[i] = Math.abs((varValues[nVars - 1] - leftVal)) + 2 * limMargin;
      xValues[i] = (leftVal + rightVal) / 2;

      varselparams = {
         nVars,
         nInt,
         intSize,
         intLeftIndices,
         intRightIndices,
         intLeftValues,
         intWidth,
         xValues
      };
   } else {
      varselparams = null;
   }

   function excludeVars(X, varlist) {
      const X_new = Matrix.zeros(X.nrows, X.ncols - varlist.length);
      const nrows = X.nrows;
      let c = 0;
      //console.log('excluded ' + varlist.length + ' variables, new size: ' + X_new.nrows + 'x' + X_new.ncols);
      for (let i = 0; i < X.ncols; i++) {
         if (varlist.includes(i)) continue;
         const offset_0 = nrows * i;
         const offset_1 = nrows * c;
         X_new.v.set(X.v.slice(offset_0, offset_0 + nrows), offset_1);
         c = c + 1;
      }
      return X_new;
   }



   async function getOutcomes(X_train, X_pv, X_alt, ncomp, center, scale, statsCompare) {
      return WASM()
         .then(Module => {
            const m = pca_fit(Module, X_train, ncomp, center, scale, []);
            const res_target = pca_predict(Module, m, X_pv);
            const res_alt = pca_predict(Module, m, X_alt);
            return getStat(m, res_target, res_alt, statsCompare);
         })
         .catch(e =>{
            console.error(e)
         });
   }

   async function runOnce(datasets, model, varselparams, statsCompare) {

      const center = model.center;
      const scale = model.scale;
      const nInt = varselparams.nInt;

      const X_train = datasets.cal.values;
      const X_pv = datasets.pv.values;
      const X_alt = datasets.alt.values;

      // correct number of components if necessary
      const nVarsNew = X_train.ncols - varsExcluded.length - varselparams.intSize;
      const nComp = model.ncomp > nVarsNew ? nVarsNew : model.ncomp;

      // prepare matrices for sensitivity values and differences
      const sens = Matrix.zeros(nComp, nInt);
      const spec = Matrix.zeros(nComp, nInt);
      const eff = Matrix.zeros(nComp, nInt);
      const sel = Matrix.zeros(nComp, nInt);

      const sens_diff = Matrix.zeros(nComp, nInt);
      const spec_diff = Matrix.zeros(nComp, nInt);
      const eff_diff = Matrix.zeros(nComp, nInt);
      const sel_diff = Matrix.zeros(nComp, nInt);

      for (let i = 0; i < nInt; i++) {
         if (intExcluded.length > 0 && intExcluded.includes(i)) continue;
         outcomes = {
            status: 'running',
            curInt: i,
         };

         // excluded i-th interval
         const intVarInd = seq(varselparams.intLeftIndices[i], varselparams.intRightIndices[i])
         const varInd = intSize === 1 ? [...varsExcluded, i] : varsExcluded.concat(intVarInd);
         const X_train_i = excludeVars(X_train, varInd);
         const X_pv_i = excludeVars(X_pv, varInd);
         const X_alt_i = excludeVars(X_alt, varInd);

         // get statistics and add to matrices
         const stat_i = await getOutcomes(X_train_i, X_pv_i, X_alt_i, nComp, center, scale, statsCompare);
         sens.v.set(stat_i.sens.val, i * nComp);
         spec.v.set(stat_i.spec.val, i * nComp);
         eff.v.set(stat_i.eff.val, i * nComp);
         sel.v.set(stat_i.sel.val, i * nComp);

         sens_diff.v.set(stat_i.sens.diff, i * nComp);
         spec_diff.v.set(stat_i.spec.diff, i * nComp);
         eff_diff.v.set(stat_i.eff.diff, i * nComp);
         sel_diff.v.set(stat_i.sel.diff, i * nComp);

      }

      outcomes = {
         status: 'done',
         ncomp: nComp,
         sens: {val: sens.t(), diff: sens_diff.t()},
         spec: {val: spec.t(), diff: spec_diff.t()},
         eff: {val: eff.t(), diff: eff_diff.t()},
         sel: {val: sel.t(), diff: sel_diff.t()},
      };
   }

   function doIt(varselparams) {
      outcomes = null;
      runOnce(datasets, model, varselparams, statsBest ? statsBest : statsGlobal);
   }
</script>

<Container label="">
   <p class="title">Variable selection settings</p>
</Container>

{#if intExcluded.length === 0}
<Container label='Interval width:'>
   <Select id='intSize' bind:value={intSize} options={intSizeOptions} />
</Container>
{:else}
<Container>
   <p class="excluded">
      Excluded: {@html intExcluded.map(v => '<span>' + (v + 1) + '</span>').join(' ')}
   </p>
   <ButtonCancel on:click={() => dispatch('resetexcluded')} />
</Container>
{/if}

<Container label='&nbsp;'>
   <Button id='run' on:click={() => doIt(varselparams)}>Run selection</Button>
</Container>


<style>
   .excluded{
      line-height: 1.5em;
      text-align: left;
      padding: 0.5em 0;
      flex: 0 0 80%;
   }

   .excluded > :global(span) {
      color: #DC143C;
      background: #DC143C20;

      display: inline-block;
      white-space: nowrap;
      vertical-align: middle;
      max-width: 10ch;
      overflow: hidden;
      text-overflow: ellipsis;

      padding: 0.1em 0.5em;
      margin: 0.2em 0.1em;
      border-radius: 0.2em;
   }
</style>