<script>
   /******************************************************************/
   /* ResultsTable                                                   */
   /* -------------------------                                      */
   /* shows table with SIMCA-LOVE results global and current models  */
   /******************************************************************/

   // main properties of the whole app
   export let statsGlobal;
   export let statsBest;

   const fomNames = {
      'sens': 'sensitivity',
      'spec': 'specificity',
      'eff': 'efficiency',
      'sel': 'selectivity'
   };

   function stat2st(v, ncomp) {
      return `${v.toFixed(3)} (${ncomp < 10 ? '0' + ncomp : ncomp})`;
   }

   function getRes(stats) {
      let str = ['sens', 'spec', 'eff', 'sel']
      let max = [-1, -1, -1, -1];
      let comp = [0, 0, 0, 0];

      for (let i = 0; i < stats.ncomp; i++) {
         for (let j = 0; j < str.length; j++) {
            const v = stats[str[j]][i];
            if (v > max[j]) {
               max[j] = v;
               comp[j] = i + 1;
            }
         }
      }

      return {max, comp, str}
   }

   $: gres = statsGlobal ? getRes(statsGlobal) : null;
   $: bres = statsBest ? getRes(statsBest) : null;
</script>


{#if gres && bres}
<table>
   <tr>
      <th></th>
      <th>Global</th>
      <th>Current</th>
   </tr>
   {#each gres.str as v, i}
   <tr>
      <td class="label">{fomNames[v]}</td>
      <td>{stat2st(gres.max[i], gres.comp[i])}</td>
      <td>{stat2st(bres.max[i], bres.comp[i])}</td>
   </tr>
   {/each}
</table>
{/if}

<style>
   table {
      width: 100%;
      border-collapse: collapse;
      font-size: 0.85em;
   }

   tr {
      width: 100%;
   }

   th {
      border-bottom: 1px solid #a0a0a0;
   }

   th, td {
      text-align: right;
      padding: 0.35em 0.5em 0.35em 0.1em;
      color: #404040;
      margin: 0;
   }

   .label {
      text-align: left;
      padding: 0.1em 0.5em;
   }
</style>