<script>
   /**************************************************************/
   /* --------------------                                       */
   /**************************************************************/

   import { Segments, TextLabels, Rectangles } from 'svelte-plots-basic/2d';

   export let limX;
   export let ncomp;
   export let statsGlobal;
   export let statsBest;
   export let outcomes;
   export let varselparams;
   export let fom = 'sens';
   export let fomView = 'diff';
   export let selected;
   export let seriesColors;


   // coordinates of rectangle showing currently processed interval
   let pr;
   $: if (outcomes && outcomes.status === 'running') {
      const i = outcomes.curInt;
      pr = {
         left: [varselparams.intLeftValues[i]],
         width: [varselparams.intWidth[i]],
         top: [2],
         height: [4],
         faceColor: '#fffa0040' // yellowish
      }
   } else {
      pr = null;
   }

   // coordinates of rectangles to show bar plots with statistics (and global value)
   let ps, globValue, bestValue, globXValue;
   $: if (fomView && outcomes && outcomes.status === 'done') {
      const y = outcomes[fom][fomView].getcolumn(ncomp).v;
      const x = varselparams.xValues;
      const w = varselparams.intWidth;

      const n = x.length;
      const left = Array(n);
      const top = Array(n);
      const height = Array(n);
      const width = Array(n);

      for (let i = 0; i < n; i++) {
         left[i] = x[i] - 0.4 * w[i],
         width[i] = w[i] * 0.8;
         if (y [i] >= 0) {
            top[i] = y[i];
            height[i] = y[i];
         } else {
            top[i] = 0;
            height[i] = -y[i];
         }
      }
      ps = {left, top, height, width, faceColor: '#a8a04080'};
      globValue = statsGlobal[fom][ncomp - 1];
      globXValue = (limX[0] + left[0]) * 0.5;
   } else {
      ps = null;
      globValue = 0;
   }

   $: bestValue = statsBest ? statsBest[fom][ncomp - 1] : null;

   // coordinates of rectangle to show bar plots with statistics for selected interval
   let ps_sel, tl_sel;
   $: if (ps && selected >= 0) {
      const left = ps.left[selected];
      const width = ps.width[selected];
      const top = ps.top[selected];
      const height = ps.height[selected];
      const value = top === 0 ? -height : height;

      ps_sel = {
         left:  [left],
         width: [width],
         top: [top],
         height: [height],
         faceColor: seriesColors.selected
      }

      tl_sel = {
         xValues: [left + width * 0.5],
         yValues: [value],
         labels: [value.toFixed(3)],
         pos: value < 0 ? 1 : 3,
         textSize: 0.85,
         faceColor: seriesColors.selected
      }

   } else {
      ps_sel = null;
      tl_sel = null;
   }
</script>

{#if outcomes}

   {#if pr}
      <!-- currently processed interval -->
      <Rectangles {...pr}  />
   {/if}

   {#if outcomes.status === 'done'}

      {#if fomView === 'diff'}
         <!-- horizontal line at y = 0 -->
         <Segments xStart={[limX[0]]} xEnd={[limX[1]]} yStart={[0]} yEnd={[0]} lineColor='#808080'/>
      {:else}
         {#if globValue}
         <!-- horizontal line at y = statistic for global and best model -->
         <Segments xStart={[limX[0]]} xEnd={[limX[1]]} yStart={[globValue]} yEnd={[globValue]} lineColor={seriesColors.global}/>
         <TextLabels xValues={[globXValue]} yValues={[globValue]} labels={[globValue.toFixed(3)]} pos={globValue > bestValue ? 3 : 1} textSize={0.85} faceColor={seriesColors.global}/>
         {/if}
         {#if bestValue}
         <Segments xStart={[limX[0]]} xEnd={[limX[1]]} yStart={[bestValue]} yEnd={[bestValue]} lineType={3} lineColor={seriesColors.best}/>
         <TextLabels xValues={[globXValue]} yValues={[bestValue]} labels={[bestValue.toFixed(3)]} pos={globValue > bestValue ? 1 : 3} textSize={0.85} faceColor={seriesColors.best}/>
         {/if}
      {/if}

      {#if ps}
      <!-- bar plot with statistics -->
      <Rectangles {...ps} />
      {/if}

      {#if ps_sel}
      <!-- bar plot and label with statistic for selected interval -->
      <Rectangles {...ps_sel} />
      <TextLabels {...tl_sel} />
      {/if}

   {/if}
{/if}