<script>
   /**************************************************************/
   /* --------------------                                       */
   /**************************************************************/

   import { mean, sd } from 'mdatools/stat';
   import { vector, Vector } from 'mdatools/arrays';
   import { Lines } from 'svelte-plots-basic/2d';

   export let datasets;                      // JSON with PCA model
   export let limY;

   let xValues, mX, sX, lower, upper, range;
   $: if (datasets) {
      const n = datasets.cal.varvalues.length;
      xValues = vector(datasets.cal.varvalues);
      mX = datasets.cal.values.apply(v => mean(v), 2);
      sX = datasets.cal.values.apply(v => sd(v), 2);
      lower = 9999;
      upper = -9999;
      for (let i = 0; i < n; i++) {
         const syu_i = mX.v[i] + sX.v[i]
         const syb_i = mX.v[i] - sX.v[i]
         lower = Math.min(lower, syb_i);
         upper = Math.max(upper, syu_i);
         range = upper - lower;
      }
   } else {
      mX = null;
      sX = null;
      xValues = null;
   }


   let my, syu, syb;
   $: if (mX && sX && limY) {

      let limRange = limY[1] - limY[0];
      let minLim = limY[0] + limRange * 0.1;
      let maxLim = limY[1] - limRange * 0.3;
      limRange = maxLim - minLim;

      const n = mX.length;
      my = Vector.zeros(n);
      syu = Vector.zeros(n);
      syb = Vector.zeros(n);

      for (let i = 0; i < n; i++) {
         const m_i = mX.v[i];
         const s_i = sX.v[i];
         const syu_i = m_i + s_i;
         const syb_i = m_i + s_i;

         syu.v[i] = (syu_i - lower) / range * (limRange) + minLim;
         syb.v[i] = (syb_i - lower) / range * (limRange) + minLim;
         my.v[i] = (m_i - lower) / range * (limRange) + minLim;
      }
   } else {
      my = null;
   }
</script>


{#if my}
<Lines {xValues} yValues={my}  lineColor='#80808080' lineWidth={2}/>
<Lines {xValues} yValues={syb} lineColor='#80808080' lineWidth={2} lineType={3}/>
<Lines {xValues} yValues={syu} lineColor='#80808080' lineWidth={2} lineType={3}/>
{/if}
